/*<! END TYPOGRAPHY !>*/
/*<! START LAYOUT !>*/
/* Layout */
$grid: 1440;
$gap: 25;
//$gap: 50;
$space: 50; /* Space variable is a seed for how much padding in the layout */
$edge: 146;
$mobileEdge:25;
/*<! END LAYOUT !>*/



$edgeclamp: clamp($mobileEdge * 1px, roundit( calc($edge / $grid), 2) * 100vw, $edge * 1px);
$borderradius: 8px;
$standardgap: clamp( roundit($gap * .75, 2) * 1px,roundit( calc($gap / $grid), 2) * 100vw, ($gap * 1px) );

$indexleft: 35%;
$indexright: 65%;

